import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import IconButton from '@material-ui/core/IconButton';
import InputAdornment from '@material-ui/core/InputAdornment';
import SearchIcon from '@material-ui/icons/Search';
import { useRouteChanger } from '../../utils/RouteChanger';
import {encode as base64_encode} from 'base-64';

function LandingPage() {

    const classes = useStyles();
    const [query, setQuery] = useState('');
    const changeRoute = useRouteChanger();

    const handleTextBarChange = (e) => {
        setQuery(e.target.value);
    }

    const handleScan = () => {
        let encodedQuery = base64_encode(query)
        encodedQuery = encodedQuery.replace('/', '_') // hack
        changeRoute(`/analysis/${encodedQuery}`)
    }

    return (
        <div>
            <Typography className={classes.title}>
                MAHCOFFEE
            </Typography>
            <Typography className={classes.subtitle}>
                Scan suspicious URLs and links to downloadable content to detect malware
            </Typography>
            <Typography className={classes.subtitle2}>
                and other breaches from multiple malware databases.
            </Typography>
            <TextField
                className={classes.searchBar}
                placeholder='Analyze or scan a URL'
                onInput={(e) => handleTextBarChange(e)}
                onKeyPress={(e) => e.key === 'Enter' ? handleScan() : ''}
                value={query}
                spellCheck={false}
                margin="normal"
                InputProps= {{
                    endAdornment: (
                        <InputAdornment position="end">
                            <IconButton onClick={() => handleScan()}
                                        className={classes.iconButton}
                                        aria-label="search">
                                <SearchIcon />
                            </IconButton>
                        </InputAdornment>
                    ),
                    classes: {
                        root: classes.root,
                        focused: classes.focused,
                        notchedOutline: classes.notchedOutline,
                        input: classes.input,
                    },
                }}
                variant="outlined"
            />
        </div>
    );
}

export default LandingPage;

const useStyles = makeStyles((theme) => ({
    root: {
        '&$focused $notchedOutline': {
           borderColor: '#87CEFA'
        },
        '&:hover $notchedOutline': {
            borderColor: '#87CEFA'
        },
        borderRadius: 0,
    },
    focused: {},
    notchedOutline: {},
    title: {
        color: '#87CEFA',
        fontWeight: 'bold',
        fontFamily: 'Monospace',
        fontSize: 100,
        paddingTop: '150px',
        marginBottom: '20px',
    },
    subtitle: {
        fontFamily: 'Monospace',
    },
    subtitle2: {
        fontFamily: 'Monospace',
        marginBottom: '30px',
    },
    searchBar: {
        margin: '0 !important',
        width: '50%',
        color: '#87CEFA',
    },
    iconButton: {
        padding: 10,
        '&:hover': {
            backgroundColor: 'white'
        }
    },
    input: {
        fontFamily: 'Monospace',
    },
}));