export function analyzeURL(encodedQuery, callback) {
    // TODO change host
    const endpoint = 'http://34.83.41.84:5000/VT'
    const toFetch = `${endpoint}/${encodedQuery.link}`;
    console.log('FETCH', toFetch);
    fetch(toFetch)
        .then(response => response.json())
        .then(data => callback(data))
}

