import React, { useState, useEffect } from "react";
import { makeStyles } from '@material-ui/core/styles';
import { useParams } from "react-router-dom";
import { analyzeURL, analyzeURLTest } from "../../utils/InterfaceFunctions";
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import CheckCircleOutlinedIcon from '@mui/icons-material/CheckCircleOutlined';
import CancelOutlinedIcon from '@mui/icons-material/CancelOutlined';
import DoNotDisturbOnOutlinedIcon from '@mui/icons-material/DoNotDisturbOnOutlined';
import Grid from '@material-ui/core/Grid'
import { Typography } from "@material-ui/core";

function AnalysisPage() {

    const classes = useStyles();
    const [analysisJSON, setAnalysisJSON] = useState(null)
    const encodedQuery = useParams();

    const leftGridStyle = { borderTop: "1px solid grey", borderLeft: "1px solid grey" };
    const rightGridStyle = { borderTop: "1px solid grey", borderRight: "1px solid grey" };
    const leftBottomGridStyle = { borderTop: "1px solid grey", borderLeft: "1px solid grey", borderBottom: "1px solid grey" };
    const rightBottomGridStyle = { borderTop: "1px solid grey", borderRight: "1px solid grey", borderBottom: "1px solid grey" };


    const handleAnalysisJSON = (incomingJSON) => {
        if (incomingJSON && Object.keys(incomingJSON["analysis"]).length % 2 === 1) {
            incomingJSON["analysis"][""] = "";
        }
        console.log(incomingJSON);
        setAnalysisJSON(incomingJSON);
    }

    useEffect(() => {
        analyzeURL(encodedQuery, handleAnalysisJSON);
    }, [encodedQuery])

    useEffect(() => {
    }, [analysisJSON])

    function generateList(analysisList, gridStyle, bottomStyle) {
        return (
            <List>
                {analysisJSON && analysisList.map((name, index) => {
                    return (
                        <ListItem key={name} style={index === analysisList.length - 1 ? bottomStyle : gridStyle}>
                            <ListItemIcon>
                                {index === analysisList.length - 1 && name === "" &&
                                    <CheckCircleOutlinedIcon style={{fill: "white"}}/>}
                                {analysisJSON["analysis"][name] === "clean" && 
                                    <CheckCircleOutlinedIcon style={{fill: "#32cd32"}}/>}
                                {analysisJSON["analysis"][name] === "suspicious" && 
                                    <CancelOutlinedIcon style={{fill: "#ffc400"}}/>}
                                {analysisJSON["analysis"][name] === "malicious" && 
                                    <CancelOutlinedIcon style={{fill: "#FF0000"}}/>}
                                {name !== "" && analysisJSON["analysis"][name] !== "clean" &&
                                    analysisJSON["analysis"][name] !== "suspicious" &&
                                    analysisJSON["analysis"][name] !== "malicious" && 
                                    <DoNotDisturbOnOutlinedIcon style={{fill: "lightgray"}}/>}
                            </ListItemIcon>
                            <ListItemText disableTypography className={classes.text} primary={name} />
                        </ListItem>
                    )
                })}
            </List>
        );
    }

    return (
        <div className={classes.main}>
            {analysisJSON &&
                <Typography className={classes.score}>
                    Score: {analysisJSON["analysis_score"]}
                </Typography>
            }
            {!analysisJSON &&
                <Typography className={classes.score}>
                    Score: N/A
                </Typography>
            }
            <Grid container
                spacing={0}
                direction="row"
                justifyContent="center"
            >
                <Grid container item xs={5} 
                    direction="column"
                >
                    {analysisJSON && 
                        generateList(Object.keys(analysisJSON["analysis"]).splice(0, Math.ceil(Object.keys(analysisJSON["analysis"]).length / 2)), leftGridStyle, leftBottomGridStyle)}
                </Grid>
                <Grid container item xs={5} 
                    direction="column"
                >
                    {analysisJSON &&
                        generateList(Object.keys(analysisJSON["analysis"]).splice(Math.ceil(Object.keys(analysisJSON["analysis"]).length / 2)), rightGridStyle, rightBottomGridStyle)}
                </Grid>
            </Grid>
        </div>
    )
}

export default AnalysisPage;

const useStyles = makeStyles((theme) => ({
    text: {
        fontFamily: "Monospace",
        paddingLeft: '30px',
    },
    main :{
        paddingTop: '110px',
        paddingBottom: '125px',
    },
    score: {
        color: 'black',
        fontWeight: 'bold',
        fontFamily: 'Monospace',
        fontSize: 25,
    },
}));